import React, { Component } from 'react';
import { Button, Modal, ModalHeader, ModalBody, ModalFooter } from 'reactstrap';
import { performItemPost, deleteItem, fetchItems, likeItem } from '../actions/itemActions';
import { connect } from 'react-redux';
import { ITEMS_URL, USER_URL } from '../listrUrls';
import PropTypes from 'prop-types'

class ListDetail extends Component {

  constructor(props){
    super(props);
    this.state = {
      itemName: null,
      itemDescription: null,
      loadedOnce: false,
    }
    this.addItem = this.addItem.bind(this);
    this.deleteItem = this.deleteItem.bind(this);
  }

  addItem(e){
    e.preventDefault();
    document.getElementById("newItemForm").reset();
    const itemData = {
      "name": this.state.itemName,
      "static_id": this.makeid(),
    }
    if(this.state.itemDescription){
      itemData.description = this.state.itemDescription;
    }
    this.props.performItemPost(ITEMS_URL, itemData, this.props.data.static_id, this.props.token);
    this.setState({
      itemName: null,
      itemDescription: null,
    });
  }

  deleteItem(e, itemID, listID){
    e.preventDefault();
    this.props.deleteItem(ITEMS_URL, itemID, listID, this.props.token);
  }

  likeItem(e, itemData, listID){
    e.preventDefault();
    fetch(USER_URL, {
      method: 'GET',
      headers: {
        'content-type': 'application/json',
        'Authorization': 'Token ' + this.props.token,
      },
    })
    .then(res => res.json())
    .then(data => {
      if(data.username){
        itemData.liked_users.push(data.username);
        this.props.likeItem(ITEMS_URL, itemData, listID, this.props.token);
      }
    });
  }

  makeid() {
    var text = "";
    var possible = "ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789";
    for (var i = 0; i < 8; i++)
      text += possible.charAt(Math.floor(Math.random() * possible.length));
    return text;
  }

  handleChange = (e) => {
    e.preventDefault();
    this.setState({[e.target.id]: e.target.value});
  }

  render () {
    var data;
      if(this.props.isOpen && this.props.loading){
        data = (
          <div class="card">
            <div class="card-header text-center" id="headingOne">
              <h5 class="mb-0">
                <a class="btn btn-link disabled" type="button">
                  Loading
                </a>
              </h5>
            </div>
          </div>
        );
        this.props.fetchItems(ITEMS_URL, this.props.data.static_id, this.props.token);
      }
      if(this.props.isOpen && !this.props.loading && this.props.items && this.props.items.length > 0){
        data = this.props.items.map(item => {
            var liked;
            if(item.liked_users.length > 0){
              liked = <span class="badge badge-danger badge-pill">{item.liked_users.length}</span>
            }
            if(item.description !== ""){
              const target = "#"+item.static_id;
              const showNewLine = {
                whiteSpace: "pre-wrap"
              }
              return (
              <div class="card">
                <div class="card-header">
                  <div class="d-flex w-100 justify-content-between">
                    <h5 class="mb-0">
                      <button class="btn btn-primary" type="button" data-toggle="collapse" data-target={target} aria-expanded="false" aria-controls={target}>
                        {item.name} - {item.owner}
                      </button>
                    </h5>
                    <div class="d-flex justify-content-between align-items-center text-right">
                      {liked}
                      <button type="button" class="btn btn-link" onClick={e => this.likeItem(e, item, this.props.data.static_id)}><i class="fa fa-heart text-primary"></i></button>
                      <button type="button" class="btn btn-link" onClick={e => this.deleteItem(e, item.static_id, this.props.data.static_id)}><i class="fa fa-times text-danger"></i></button>
                    </div>
                    </div>
                </div>
                <div id={item.static_id} class="collapse" aria-labelledby={item.static_id} data-parent="#itemAccordian">
                  <div class="card-body" style={showNewLine}>
                    {item.description}
                  </div>
                </div>
              </div>
            );
          }else{
            return (
              <div class="card" key={item.static_id}>
                <div class="card-header d-flex justify-content-between" id="headingOne">
                  <div class="d-flex w-100 justify-content-between align-items-center">
                    <h5 class="mb-0">
                      <p class="btn" disabled>
                        {item.name} - {item.owner}
                      </p>
                    </h5>
                    <div class="text-right">
                      {liked}
                      <button type="button" class="btn btn-link" onClick={e => this.likeItem(e, item, this.props.data.static_id)}><i class="fa fa-heart text-primary"></i></button>
                      <button type="button" class="btn btn-link" onClick={e => this.deleteItem(e, item.static_id, this.props.data.static_id)}><i class="fa fa-times text-danger"></i></button>
                    </div>
                </div>
                </div>
              </div>
            );
          }
        });
      }else{
        data = (
          <div class="card">
            <div class="card-header text-center" id="headingOne">
              <h5 class="mb-0">
                <a class="btn btn-link disabled" type="button">
                  This List has no items in it. Why not add one?
                </a>
              </h5>
            </div>
          </div>
        );
      }
      return(
        <Modal isOpen={this.props.isOpen} toggle={this.props.toggle} centered={true}>
          <ModalHeader toggle={this.props.toggle}>
            {this.props.data.name}
          </ModalHeader>
          <ModalBody>
            <form onSubmit={e => this.addItem(e)} id="newItemForm">
              <div class="form-group">
                <label htmlFor="itemName" class="bmd-label-floating">Item Name</label>
                <input type="text" class="form-control" autoComplete="off" id="itemName" onChange={e => this.handleChange(e)}required />
              </div>
              <div class="form-group">
                <label htmlFor="itemDescription" class="bmd-label-floating">Item Description(Optional)</label>
                <textarea type="text" class="form-control" autoComplete="off" id="itemDescription" onChange={e => this.handleChange(e)}/>
              </div>
              <button type="submit" class="btn btn-raised btn-primary">Add Item</button>
            </form>
            <hr />
            <div class="accordion" id="itemAccordian">
            {data}
            </div>
          </ModalBody>
        </Modal>
      );
  }
}

ListDetail.proptypes = {
  performItemPost: PropTypes.func.isRequired,
  deleteItem: PropTypes.func.isRequired,
  likeItem: PropTypes.func.isRequired,
  fetchItems: PropTypes.func.isRequired,
}

const mapStateToProps = state => ({
  token: state.users.token,
  loading: state.items.loading,
  items: state.items.items
});

export default connect(mapStateToProps, { performItemPost, deleteItem, fetchItems, likeItem })(ListDetail);



